const animatedScrollObserval = new IntersectionObserver(
    (entries, animatedScrollObserval) => {
        entries.forEach((entry) => {
            if (entry.isIntersecting) {
                entry.target.classList.add('enter');
                animatedScrollObserval.unobserve(entry.target)
            }
        })
    }
)

export default {
    bind(el) {
        el.classList.add('before-enter');
        animatedScrollObserval.observe(el);
    }
}