<template>
  <div id="burger" :class="{ 'active' : isBurgerActive }" @click.prevent="toggle">
    <slot>
      <button type="button" class="toggle" title="Menu">
        <span class="toggle__item toggle__item--one"></span>
        <span class="toggle__item toggle__item--two"></span>
        <span class="toggle__item toggle__item--three"></span>
      </button>
    </slot>
  </div>
</template>

<script>
import { store, mutations } from "@/main.js";
export default {
  computed: {
    isBurgerActive() {
      return store.isNavOpen;
    }
  },
  methods: {
    toggle() {
      mutations.toggleNav();
    }
  }
};
</script>

<style scoped>
.hidden {
  visibility: hidden;
}

button {
  cursor: pointer;
}

button:focus {
  outline: 0;
}

.toggle {
  position: relative;
  height: 30px;
  width: 32px;
  display: block;
  float: right;
  z-index: 999;
  border: 0;
  border-radius: 0;
  background-color: transparent;
  pointer-events: all;
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.toggle__item {
  background-color: #130f40;
  position: absolute;
  top: 50%;
  right: 6px;
  left: 6px;
  height: 2px;
  width: auto;
  margin-top: -1px;
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1),
    opacity 0.3s cubic-bezier(0.165, 0.84, 0.44, 1),
    background-color 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.toggle__item--one {
  -webkit-transform: translateY(-6px);
  transform: translateY(-6px);
}

.toggle__item--two {
  transform-origin: 100% 50%;
  transform: scaleX(0.8);
}

.toggle:hover .toggle__item--two {
  transform: scaleX(1);
}

.no-touchevents .toggle__item--two:hover {
  transform: scaleX(1);
}

.toggle__item--three {
  transform: translateY(6px);
}

#burger {
  position: absolute;
  right: 0;
  height: auto;
  z-index: 2000;
}

#burger.active .toggle {
  transform: rotate(-180deg);
}

#burger.active .toggle__item {
  background-color: #7beec7;
}

#burger.active .toggle__item--one {
  transform: rotate(45deg);
}

#burger.active .toggle__item--two {
  opacity: 0;
}

#burger.active .toggle__item--three {
  transform: rotate(-45deg);
}
</style>