<template>
  <div class="sidebar">
    <div class="sidebar-backdrop" @click="closeSidebarPanel" v-if="isPanelOpen"></div>
    <transition name="slide">
      <div v-if="isPanelOpen" class="sidebar-panel">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script>
import { store, mutations } from "@/main.js";
export default {
  methods: {
    closeSidebarPanel() {
      mutations.toggleNav();
    }
  },
  computed: {
    isPanelOpen() {
      return store.isNavOpen;
    }
  }
};
</script>

<style scoped>
.slide-enter-active,
.slide-leave-active {
  transition: transform 0.5s ease;
}

.slide-enter,
.slide-leave-to {
  transform: translateX(-100%);
  transition: all 0.5s ease-in 0s;
}

.sidebar-backdrop {
  width: 100vw;
  height: 50vh;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}

.sidebar-panel {
  overflow-y: auto;
  position: absolute;
  right: 0;
  top: 0;
  height: 50vh;
  z-index: 999;
  padding: 0 20px 2rem 20px;
}
</style>